.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: calc(100vh - 80px); /* ヘッダー分差し引き */
  background-image: url("/public/images/home/hero-img.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 2rem;
}

.hero-content {
  max-width: 800px;
}

.hero-title {
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 1.5rem;
}

.hero-subtitle {
  font-size: 1.3rem;
  line-height: 1.8;
  color: #ffffff;
  margin-bottom: 1.5rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  word-break: break-word;
}

.hero-button {
  padding: 12px 24px;
  background-color: #007bff;
  color: #ffffff;
  text-decoration: none;
  border-radius: 5px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.hero-button:hover {
  background-color: #0056b3;
}

@media (max-width: 600px) {
  .hero {
    height: auto;
    padding: 3rem 1rem;
    background-position: center top; /* 必要なら背景の表示位置調整 */
  }

  .hero-title {
    font-size: 2rem; /* スマホ向けに小さく */
  }

  .hero-subtitle {
    font-size: 1rem;
    padding: 0 1rem;
    text-align: center;
    word-break: break-word
  }

  .hero-button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

